// src/pages/Player.js
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Header from "../components/Header";
import SongList from "../components/SongList";
import SongItem from "../components/SongItem";
import Controls from "../components/Controls";
import LoadingAnimation from "../components/LoadingAnimation"; // Importăm componenta de încărcare
import { selectSongs } from "../redux/slices/songsSlice";

const Player = () => {
   const { songs } = useSelector(selectSongs);
   const [loading, setLoading] = useState(true);

   useEffect(() => {
      // Verificăm dacă datele pentru primele două melodii au fost încărcate
      if (songs.length > 1 && songs[0].img && songs[1].img) {
         setTimeout(() => {
            setLoading(false);
         }, 3000); // Setăm durata totală de așteptare la 1 secundă suplimentară
      }
   }, [songs]);

   return (
      <div className="App wrapper">
         {loading && <LoadingAnimation setLoading={setLoading} />}
         <div className="player">
            {/*<Header />*/}
            <SongItem />
            <Controls />
            <SongList />
         </div>
      </div>
   );
};

export default Player;
