import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectSongs } from "../redux/slices/songsSlice";
import { Palette } from "color-thief-react";

const SongItem = () => {
   const songsState = useSelector(selectSongs);
   const { currentSongIndex } = songsState;
   const currentSong = songsState.songs[currentSongIndex];
   const imgSrc = currentSong.img;

   const [accentColor, setAccentColor] = useState("");

   // Funcția pentru actualizarea culorilor în funcție de modul dark/light
   const updateColors = (colors) => {
      const body = document.body;
      const isDarkMode = body.classList.contains("darkmode");

      const mainColor = isDarkMode ? colors.darkestColor : colors.lightestColor;
      const accentColor = isDarkMode
         ? colors.lightestColor
         : colors.darkestColor;

      body.style.setProperty("--mainColor", mainColor);
      body.style.setProperty("--accentColor", accentColor);

      setAccentColor(accentColor);
   };

   useEffect(() => {
      // Definim un observator pentru a detecta schimbarea clasei `darkmode`
      const body = document.body;
      const observer = new MutationObserver(() => {
         // Când clasa `darkmode` se schimbă, actualizăm culorile
         const isDarkMode = body.classList.contains("darkmode");
         updateColors({
            lightestColor: isDarkMode ? "hsl(0, 0%, 100%)" : "hsl(0, 0%, 0%)",
            darkestColor: isDarkMode ? "hsl(0, 0%, 0%)" : "hsl(0, 0%, 100%)",
            accentColor: isDarkMode ? "hsl(0, 50%, 70%)" : "hsl(0, 50%, 30%)",
         });
      });

      // Observăm schimbările de clasă pe `body`
      observer.observe(body, { attributes: true, attributeFilter: ["class"] });

      // Curățăm observatorul la demontarea componentei
      return () => observer.disconnect();
   }, []);

   return (
      <div
         className={`song-item ${songsState.songsListOpen ? "list-open" : ""}`}
      >
         <img className="song-item__img-bg" src={imgSrc} alt="Song Cover" />
         <div className="song-item__bg"></div>
         <div className="song-item__wrapper">
            <img className="song-item__img" src={imgSrc} alt="Song Cover" />

            <Palette
               src={imgSrc}
               crossOrigin="anonymous"
               format="hslString"
               colorCount={8}
               style={{ display: "none" }}
            >
               {({ data }) => {
                  if (!data) return null;

                  // Calculăm cea mai întunecată și cea mai luminoasă culoare
                  const darkestColor = data.reduce((darkest, color) => {
                     const hslRegex = /hsl\((\d+),\s*(\d+)%,\s*(\d+)%\)/;
                     const [, , , lightness] = color.match(hslRegex);
                     return parseInt(lightness) <
                        parseInt(darkest.match(hslRegex)[3])
                        ? color
                        : darkest;
                  }, data[0]);

                  const lightestColor = data.reduce((lightest, color) => {
                     const hslRegex = /hsl\((\d+),\s*(\d+)%,\s*(\d+)%\)/;
                     const [, , , lightness] = color.match(hslRegex);
                     return parseInt(lightness) >
                        parseInt(lightest.match(hslRegex)[3])
                        ? color
                        : lightest;
                  }, data[0]);

                  updateColors({
                     lightestColor: lightestColor,
                     darkestColor: darkestColor,
                  });

                  return null;
               }}
            </Palette>
         </div>
      </div>
   );
};

export default SongItem;
