// src/components/LoadingAnimation.js
import React, { useEffect, useState } from "react";
import musicList from "../data/musicList";

const LoadingAnimation = ({ setLoading }) => {
   const [loaded, setLoaded] = useState(false);

   useEffect(() => {
      const loadImageAndMusic = async () => {
         try {
            // Simulează încărcarea primei imagini
            const img = new Image();
            img.onload = () => {
               // Așteaptă o scurtă perioadă după încărcare pentru efectul de fade-out
               setLoaded(true);
               setTimeout(() => {
                  setLoading(false); // Setează loading pe false după o secundă suplimentară
               }, 3000);
            };
            img.src = musicList[0].img; // URL-ul primei imagini din musicList

            // Simulează încărcarea primei melodii
            const audio = new Audio(musicList[0].src); // URL-ul primei melodii din musicList
            audio.oncanplaythrough = () => {
               // Nu facem nimic special aici, doar așteptăm încărcarea completă
            };
         } catch (error) {
            console.error(
               "Eroare în încărcarea imaginii sau a primei melodii:",
               error
            );
         }
      };

      // Apelăm funcția pentru a începe încărcarea imaginii și melodiei
      loadImageAndMusic();
   }, [setLoading]);

   return (
      <div className={`loading-animation ${loaded ? "loaded" : ""}`}>
         <div className="loading-animation__overlay">
            {/* Afișează un mesaj sau o altă animație pentru a indica încărcarea */}
            <div className="loading-animation__logo">80S</div>
         </div>
      </div>
   );
};

export default LoadingAnimation;
